.listings-main{
    padding:0 80px;
    min-height: 89vh;
    nav{
        padding: 50px 0px 20px;
        text-align: center;
        .dashboard-btn{
            background: none;
            font-size: 19px;
            font-weight: 800;
        }
    }
    .listings{
        
        gap : 25px ;

        .stay-list{
            display: grid;
            grid-template-columns:  repeat(auto-fit, minmax(250px, 350px)) ;
            gap : 25px ;
        }
       
    }
}